import { Component, OnInit } from "@angular/core";
import {
  faLinkedinIn,
  faAngular,
  faGithub,
} from "@fortawesome/free-brands-svg-icons";
import {
  faHeart,
  faMoon,
  faSun,
  faChevronUp,
} from "@fortawesome/free-solid-svg-icons";
import Properties, { properties } from "src/app/configurations/properties";
import { LangageService } from "src/app/services/langage.service";
import { ThemeService } from "src/app/services/theme.service";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"],
})
export class HomeComponent implements OnInit {
  public props: Properties = properties;
  public isMenuCollapsed = true;
  public isScrolled = false;
  public iconLinkedin = faLinkedinIn;
  public iconHeart = faHeart;
  public iconAngular = faAngular;
  public iconGithub = faGithub;
  public iconMoon = faMoon;
  public iconSun = faSun;
  public iconUp = faChevronUp;

  constructor(
    public langService: LangageService,
    public themeService: ThemeService
  ) {}

  ngOnInit(): void {}

  scroll = (el: HTMLElement) => {
    el.scrollIntoView({ behavior: "smooth" });
  };

  mail = () => {
    location.href = "mailto:" + properties.mail;
  };

  onScroll = (event) => {
    this.isScrolled = event.target.scrollTop > 5;
  };
}
