import { Component, HostListener } from "@angular/core";
import { LangageService } from "./services/langage.service";
import { ThemeService } from "./services/theme.service";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.css"],
})
export class AppComponent {
  private konamiCode: number[] = [38, 38, 40, 40, 37, 39, 37, 39, 66, 65];
  private currentKey: number = 0;

  constructor(
    private langService: LangageService,
    public themeService: ThemeService
  ) {
    langService.init();
  }

  @HostListener("window:keydown", ["$event"])
  keyListen = (e: KeyboardEvent) => {
    if (e.keyCode == this.konamiCode[this.currentKey++]) {
      if (this.currentKey === this.konamiCode.length) {
        const audio = new Audio();
        audio.src = window.location.pathname + "assets/wow.mp3";
        audio.load();
        audio.play();
        this.themeService.pink();
      }
    } else {
      this.currentKey = 0;
    }
  };
}
